.description {
  position: absolute;
  z-index: 20;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 10px;
  width: 28%;
  height: 100vh; }
  .description h1 {
    margin: 0;
    margin-bottom: 20px;
    font-family: "Big Shoulders Display", "Arial", sans-serif;
    font-size: 90px;
    line-height: 100px;
    letter-spacing: 0.02em;
    color: #ffffff; }
  @media (max-width: 1023px) {
    .description {
      width: 35%; } }
  @media (max-width: 767px) {
    .description {
      flex-direction: row;
      padding-top: 70px;
      width: 100%;
      height: auto; }
      .description h1 {
        font-size: 70px;
        line-height: 80px; } }

.description__text {
  font-size: 18px;
  color: #ffffff; }
  .description__text p {
    margin: 0; }
    .description__text p + p {
      margin-top: 10px; }
  @media (max-width: 1023px) {
    .description__text {
      font-size: 14px; } }
  @media (max-width: 1023px) {
    .description__text {
      font-size: 12px; } }
  @media (max-width: 767px) {
    .description__text {
      display: none; } }

.description__credits {
  display: flex;
  flex-direction: column;
  margin-top: auto; }
  .description__credits a {
    transition: opacity 0.3s ease-out;
    margin-bottom: 10px;
    color: #ffffff; }
    .description__credits a:last-child {
      margin-bottom: 0; }
    @media (min-width: 1024px) {
      .description__credits a:not(:last-child):hover {
        opacity: 0.7; } }
  @media (max-width: 1023px) {
    .description__credits {
      font-size: 14px; } }
  @media (max-width: 1023px) {
    .description__credits {
      font-size: 12px; }
      .description__credits a {
        margin-bottom: 0; } }
  @media (max-width: 767px) {
    .description__credits {
      display: none;
      margin-top: 0;
      margin-left: auto; } }

.description__author {
  opacity: 0.2;
  font-size: 14px; }
  @media (max-width: 1023px) {
    .description__author {
      font-size: 12px; } }
  @media (max-width: 1023px) {
    .description__author {
      font-size: 10px; } }

