  position: absolute;
  z-index: 50;
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation-name: rotate-orbit;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform: translate(-50%, -50%);
  perspective: 1000px;
